<template>
 <div class="prescriptionplan">
    <form class="m-5 border border-primary p-2 rounded" >
        <div class="row m-2">
        
        <h3 class="col">Rezept</h3>
        
    </div>
        <div class="row m-2">
          <div class="col">
            <input v-model="data.title" type="text" class="form-control" placeholder="Medikament">
          </div>
        </div>
        <div class="row m-2">
          <div class="col">
            <div class="form-group">
               <h6><label for="comment">Beschreibung/Anmerkung:</label></h6> 
            <textarea v-model="data.extendedProps.description" class="form-control"  rows="4" placeholder="Describe yourself here..."> </textarea>
            </div>  
        </div>
        </div>
        <div class="row m-2">
            <div class="form-group col-6">
               <h6> <label for="comment">Start</label></h6>
            <input v-model="start" class="form-control" type="date">
            </div> 
            <div class="form-group col-6">
              <h6> <label for="comment">Ende(Optional)</label></h6>
            <input v-model="end" class="form-control" type="date">
            </div> 
            

      

        </div>
        <div class="row m-2">
            <h6 class="col">Wiederholung:</h6>
        </div>
        <div class="row m-2">
            <div class="col-6">
                <select class="form-control" id="exampleFormControlSelect1">
                    <option>Montag</option>
                    <option>Dienstag</option>
                    <option>Mittwoch</option>
                    <option>Donnerstag</option>
                    <option>Freitag</option>
                    <option>Samstag</option>
                    <option>Sonntag</option>
                  </select>
    
              </div>
          <div class="col-6 ">
            
            <div class="radio">
                <label><input type="radio" name="optradio" checked>Täglich</label>
              </div>
          </div>
        </div>
        <div class="row m-2">
             
          <div class="col-6">
            <div class="radio">
                <label><input type="radio" name="optradio" checked>Wöchentlich</label>
              </div>
          </div>
          
             
          <div class="col-6">
            <div class="radio">
                <label><input type="radio" name="optradio" checked>Niemals</label>
              </div>
          </div>
         


        </div>
        <div class="row m-2">
            <h6 class="col">Wann:</h6>
        </div>
        <div class="row m-2">
           
            <div class="col-4">
              <div class="radio">
                  <label><input type="radio" name="optradio" checked>Vormittag</label>
                </div>
