      </form>
     
      {{data.start}}
      
       
    </div>
</template>

<script>


export default {
    props:{
        data:Object,
        
    },
    created:function(){
     this.start=this.data.start.toISOString().substring(0,10) ,
      this.end= this.data.end.toISOString().substring(0,10)
    },

    

    data:()=>{
        return {
      start:'',
      end:''
        }
    },
     watch: {
    data: function (val) {
       this.start=this.getLocalDate(val.start)
     if(val.end==null) this.end=''
    else  this.end=this.getLocalDate(val.end)
    }
    
  }, 
  methods:{

    getLocalDate :(val)=> {
      
      return (new Date(val.getTime() - (val.getTimezoneOffset() * 60000)).toISOString().substring(0,10));
    }
  }
  
  
}
</script>

